<template>
  <div class="bookrack">
    <HomeHeader :myBook="myBook" :isGoHome="isGoHome"> {{ goback }}</HomeHeader>
    <!-- <div class="tab">
      <div class="item">我的书架</div>
      <div class="line">|</div>
      <div class="item">阅读记录</div>
    </div> -->
    <van-tabs v-model="currentIndex">
      <van-tab
        title="我的书架"
        to="/bookrack"
        title-class="before-line"
        title-style="margin:10px 0;border-right: 1px solid #ccc;"
      ></van-tab>
      <van-tab title="阅读记录" to="/bookHistory"></van-tab>
    </van-tabs>
    <div class="bookrack-top">
      <span>共{{ books.length || 0 }}本</span>
      <span v-if="!isShowCtrl" @click.stop="isShowCtrl = true">编辑</span>
      <div v-else>
        <span @click.stop="isShowCtrl = false">取消</span>
        <span style="padding: 0 5px"></span>
        <span @click.stop="filterBook">删除</span>
      </div>
    </div>
    <BookrackItem
      v-if="books"
      :books="books"
      :isShowCtrl="isShowCtrl"
      @arr-trans="handler"
    />
  </div>
</template>

<script>
import HomeHeader from "@/components/HomeHeader.vue";
import BookrackItem from "@/components/BookrackItem.vue";

export default {
  data() {
    return {
      goback: "⮜ 返回",
      myBook: "我的书架",
      currentIndex: 0,
      books: [],
      isShowCtrl: false,
      arr: [],
      checked: false,
      isGoHome: true,
    };
  },
  components: {
    HomeHeader,
    BookrackItem,
  },
  created() {
    this.books = JSON.parse(window.localStorage.getItem("books")) || [];
  },
  computed: {},
  methods: {
    //自定义事件传上来的arr
    handler(e) {
      this.arr = e;
    },
    //筛选book，也就是删除
    filterBook() {
      this.isShowCtrl = false;
      this.books = this.books.filter((item, index) => {
        if (!this.arr[index]) {
          return item;
        }
      });
      window.localStorage.setItem("books", JSON.stringify(this.books));
      this.arr = this.books.map(() => {
        return false;
      });
    },
  },
};
</script>

<style lang="less" >
.bookrack {
  .van-tabs {
    .van-tabs__wrap {
      .van-tabs__nav {
        // .van-tab {
        // }
        .before-line {
          border-right: 1px solid #ccc !important;
        }
      }
    }
  }
  .bookrack-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    height: 60px;
    font-size: 12px;
    background-color: #f4f4f4;
  }

  .delete-button {
    height: 100%;
  }
}
</style>